import * as React from 'react';
import { Offline, Online } from 'react-detect-offline';

export function OnOffLineHint() {
  return (
    <div style={{ marginTop: '20px' }}>
      <Online>
        <p style={{ color: 'green' }}></p>
      </Online>
      <Offline>
        <p style={{ color: 'red' }}></p>
      </Offline>
    </div>
  );
}
